import { _decorator, Component, Node,CCBoolean, Vec2, find } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('FollowP')
export class FollowP extends Component {
    @property(Node)
    public player : Node;

    @property(Node)
    coche : Node;
    @property(Node)
    public SpawnP : Node; 
    @property(CCBoolean)
    public derecha = false;

    

    onLoad() {
        this.coche = find("Canvas/Player")
       /* for(let i = 0 ; i< this.player.children.length; i++)
            {
                 if(this.player.children[i].active)
                    {
                        this.coche = this.player.children[i];
                    }
            }*/
        
    }
    start() {

    }
    mueveCamara(deltaTime: number, target : Vec2){
        let currentPosition : Vec2 = new Vec2 (this.node.position.x, this.node.position.y)
        Vec2.lerp(currentPosition, new Vec2 (this.node.position.x , this.node.position.y), target , .5 * deltaTime ); 
        this.node.setPosition( currentPosition.x,currentPosition.y, 0);
    }

    update(deltaTime: number) {
        if(this.derecha){
           const targetD = new Vec2(this.coche.position.x-23,this.coche.position.y +20)
           this.mueveCamara(deltaTime,targetD)
        }
        else{
            const targetI = new Vec2(this.coche.position.x-25,this.coche.position.y +20)
            this.mueveCamara(deltaTime,targetI);
        }
    }
}


