import { _decorator, Component, Node, Quat, Vec3, director, find, Collider2D, Contact2DType, math, CCBoolean} from 'cc';
const { ccclass, property } = _decorator;

@ccclass('GiroOrbita')
export class GiroOrbita extends Component {

    @property public rotationSpeed= 0.5;
    @property public angle=0;

    @property (Node) player:Node=null;
    @property (Node) rotacion:Node=null;
    @property (Node) rotacion2:Node=null;
    rota:Boolean=false;
    public instanciar:Boolean=false;
    public cambia:Boolean=false;
    @property (Node) padre:Node=null;
    ya:Boolean=false;
    @property(CCBoolean) public primeraVez:boolean=true;

    protected onLoad(): void 
    {
        //this.player=find("Canvas/Player");
        this.rotacion=find("Canvas/IndicadorRotacion");
        this.rotacion2=find("Canvas/IndicadorRotacion-001");

        ////COLLIDER////
        let collider = this.getComponent(Collider2D);
        if (collider) 
        {
            // Called only once when two colliding bodies start to make contact
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
            
            //collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
 
        }
    }

    start() {
        this.cambia=false;
    }

    update(dt) 
    {
       if(this.player.parent==this.node&&this.rota)
            {
                this.node.angle = this.angle;

                this.angle+=this.rotationSpeed;

       
                if(this.node.angle>=360&&this.angle>=360)
                {
                    this.node.angle=0;
                    this.angle=0;
                }
            }
        

        if(this.player.parent==this.node &&!this.rota)
        {
            this.RotaHijo(this.player);
            console.log(this.node.name+"instanciar=true");
            this.instanciar=true;
            this.ya=true;
            
        }
        if(this.player.parent!=this.node&&this.ya)
            {
                console.log(this.node.name+"Cambia=true");
                this.cambia=true;
                this.instanciar=false;
                this.ya=false;
                
            }
        
    }

    RotaHijo(hijo: Node)
    {
        //hijo.setWorldRotation(this.rotacion2.getWorldRotation());
        //hijo.setWorldRotation(this.rotacion.getWorldRotation());
        
        this.player.setPosition(this.player.position.x, -85, this.player.position.z);


        let rotationGiratorio = math.toRadian(this.node.angle);

        //console.log("Angulo: " + math.toDegree(rotationGiratorio));
        let rotationQuat = new Quat();
        Quat.fromEuler(rotationQuat, 0, 0,  math.toDegree(rotationGiratorio)-90);

        let finalRotation = new Quat();
        Quat.multiply(finalRotation, this.node.rotation, rotationQuat);

        this.player.setRotation(rotationQuat);
        
        
        this.rota=true;
        
    }

    EstoySolo()
    {
        this.cambia=true;
        
    }
   

    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D)
    {
        //console.log(this.node.name+selfCollider.group);
        //console.log(this.node.name+otherCollider.group);

        if(otherCollider.group===8)
        {
            this.scheduleOnce(function(){
                this.padre.destroy();
            },2);
            
        }
        
        
    }
    onEndContact(selfCollider: Collider2D, otherCollider: Collider2D)
    {
        //console.log("termino el contacto");
        
        
        
    }
}


