import { _decorator, Component, find, Node } from 'cc';
import { PlayerController } from './NEW/PlayerController';
const { ccclass, property } = _decorator;

@ccclass('Touch')
export class Touch extends Component {
    @property (Node)
    public Player : Node = null;
    protected onLoad(): void {
        this.Player = find("Canvas/Player");
        this.node.on(Node.EventType.TOUCH_START, this.onTouchStart, this)
        this.node.on(Node.EventType.TOUCH_CANCEL,this.onTouchCancel, this)
    }
    onTouchStart(event:Touch){
        console.log("Haces cilck")
        this.Player.getComponent(PlayerController).Lanzame();
    }
    onTouchCancel(){

    }
    start() {

    }

    update(deltaTime: number) {
        
    }
}


