import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Marco')
export class Marco extends Component {

    @property(Node)
    camara: Node | null = null;

    onLoad()
    {
        
    }

    start() {

    }

    update(deltaTime: number) 
    {
        this.node.setWorldPosition(this.camara.getWorldPosition());
        this.node.setWorldRotation(this.camara.getWorldRotation());
        
    }

   
}


