import { _decorator, Component, Node, randomRangeInt, instantiate, v3, Prefab, Vec3, CCInteger, find, Vec2 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Tarjetas')
export class Tarjetas extends Component {

    @property ([Node])
    private posiciones:Node []=[];
    @property (Node)
    public tarjeta:Node = null;
    @property(Node)
    private Intsnacia : Node = null;
    @property(CCInteger)
    public position : number = 0;
  

    onLoad()
    {
        //this.tarjeta = this.node.getChildByPath("Tarjeta")
       // this.tarjeta.active=false;
    }
    start() {
       
        
        
        this.tarjeta = find("Canvas/Tarjeta")
        const node = instantiate(this.tarjeta)
        node.parent = this.node;
        this.Intsnacia = node;
        this.Mostrar();
    }

    update(deltaTime: number) {
        
    }

    Mostrar()
    {
        this.position=randomRangeInt(0, 4);

        switch(this.position)
        {
            case 0:
             
               
                this.Intsnacia.parent= this.posiciones[0];
                this.Intsnacia.setPosition(0,0,0)
               
                break;
            case 1:
                
            this.Intsnacia.parent= this.posiciones[1];
            this.Intsnacia.setPosition(0,0,0)
                
                break;
            case 2:
                
           this.Intsnacia.parent= this.posiciones[2];
           this.Intsnacia.setPosition(0,0,0)
               
                break;
            case 3:
                
           this.Intsnacia.parent= this.posiciones[3];
           this.Intsnacia.setPosition(0,0,0)   
               
                break;
        }
    }
}


