import { _decorator, Component, Node,sys, director, SceneAsset, CCBoolean } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Detecta')
export class Detecta extends Component {

   @property(Node)
   public PC : Node = null;
   @property(Node)
   public mobile : Node = null;
   @property([Node])
   public lista: Node[] =[];
   @property([Node])
   public listaFinal: Node[] =[];
   @property([Node])
   public botones: Node[]=[];
   public index: number = 0;
   @property (CCBoolean)
   public botonnext : boolean = false;


   onLoad() {
       // Verifica si el juego se está ejecutando en un dispositivo móvil
       if (sys.isMobile) {
        
        this.mobile.active = true;
        this.PC.active = false;
        this.listaFinal[0]=this.mobile;
       }
         else {
            this.mobile.active = false;
            this.PC.active = true;
            this.listaFinal[0]=this.PC;
       }

       this.listaFinal[1]=this.lista[0];
       //this.listaFinal[2]=this.lista[1];
   }

  


   start() {

    if(!this.botonnext){
        this.botones[1].active=false;
        this.botones[0].active = false;
    }else{
        this.botones[1].active=false;
    }
       

   }
   update (){

       if(this.index==0)
       {
           this.botones[1].active=false;
       }else
       {
           this.botones[1].active=true;
       }

       if(this.index==this.listaFinal.length-1)
       {
           this.botones[0].active=false;
       }
       else
       {
           this.botones[0].active=true;
       }
       
   }

   next()
   {
       if(this.index<this.listaFinal.length-1)
       {
           this.index++;

           this.listaFinal[this.index].active=true;
           this.listaFinal[this.index-1].active=false;
          
          
       }
      
   }

   back()
   {
       if(this.index>0)
       {
           this.index--;
           this.listaFinal[this.index].active=true;
           this.listaFinal[this.index+1].active=false;
          
       }

   }


  
}