import { _decorator, Component, Node, SpriteFrame, find, Sprite,Button } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('SeleccionColor')
export class SeleccionColor extends Component {

    @property([SpriteFrame]) amarillo: SpriteFrame[]=[];
    @property([SpriteFrame]) azul: SpriteFrame[]=[];
    @property([SpriteFrame]) verde: SpriteFrame[]=[];
    @property([SpriteFrame]) rojo: SpriteFrame[]=[];
    @property([SpriteFrame]) naranja: SpriteFrame[]=[];

    @property([Node]) modelos: Node[]=[];


    onLoad()
    {
        //this.modelos[0]= find("Canvas/Modelo01");
        //this.modelos[1]= find("Canvas/Modelo02");
        //this.modelos[2]= find("Canvas/Modelo03");

        this.modelos[0].children[1].children[0].getComponent(Sprite).spriteFrame=this.amarillo[1];
    }

    start() 
    {

    }

    update(deltaTime: number) {
        
    }

    Colores(color:string)
    {
        if(this.modelos[0].active)
            {
                this.Seleccion(color,0);
            }

        if(this.modelos[1].active)
            {
                this.Seleccion(color,1);
            }

        if(this.modelos[2].active)
            {
                this.Seleccion(color,2);
            }
    }

    Seleccion(color:string, modelo: number)
    {
        //console.log("cambio de color");
        switch(color)
        {
            case 'amarillo':
                this.modelos[modelo].children[1].children[0].getComponent(Sprite).spriteFrame=this.amarillo[1];
                if(this.modelos[modelo].children[1].children[1].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[1].getComponent(Sprite).spriteFrame=this.azul[0];
                if(this.modelos[modelo].children[1].children[2].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[2].getComponent(Sprite).spriteFrame=this.verde[0];
                if(this.modelos[modelo].children[1].children[3].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[3].getComponent(Sprite).spriteFrame=this.rojo[0];
                if(this.modelos[modelo].children[1].children[4].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[4].getComponent(Sprite).spriteFrame=this.naranja[0];
                break;
            case 'azul':
                this.modelos[modelo].children[1].children[0].getComponent(Sprite).spriteFrame=this.amarillo[0];
                if(this.modelos[modelo].children[1].children[1].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[1].getComponent(Sprite).spriteFrame=this.azul[1];
                if(this.modelos[modelo].children[1].children[2].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[2].getComponent(Sprite).spriteFrame=this.verde[0];
                if(this.modelos[modelo].children[1].children[3].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[3].getComponent(Sprite).spriteFrame=this.rojo[0];
                if(this.modelos[modelo].children[1].children[4].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[4].getComponent(Sprite).spriteFrame=this.naranja[0];
                break;
            case 'verde':
                this.modelos[modelo].children[1].children[0].getComponent(Sprite).spriteFrame=this.amarillo[0];
                if(this.modelos[modelo].children[1].children[1].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[1].getComponent(Sprite).spriteFrame=this.azul[0];
                if(this.modelos[modelo].children[1].children[2].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[2].getComponent(Sprite).spriteFrame=this.verde[1];
                if(this.modelos[modelo].children[1].children[3].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[3].getComponent(Sprite).spriteFrame=this.rojo[0];
                if(this.modelos[modelo].children[1].children[4].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[4].getComponent(Sprite).spriteFrame=this.naranja[0];
                break;
            case 'rojo':
                this.modelos[modelo].children[1].children[0].getComponent(Sprite).spriteFrame=this.amarillo[0];
                if(this.modelos[modelo].children[1].children[1].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[1].getComponent(Sprite).spriteFrame=this.azul[0];
                if(this.modelos[modelo].children[1].children[2].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[2].getComponent(Sprite).spriteFrame=this.verde[0];
                if(this.modelos[modelo].children[1].children[3].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[3].getComponent(Sprite).spriteFrame=this.rojo[1];
                if(this.modelos[modelo].children[1].children[4].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[4].getComponent(Sprite).spriteFrame=this.naranja[0];
                break;
            case 'naranja':
                this.modelos[modelo].children[1].children[0].getComponent(Sprite).spriteFrame=this.amarillo[0];
                if(this.modelos[modelo].children[1].children[1].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[1].getComponent(Sprite).spriteFrame=this.azul[0];
                if(this.modelos[modelo].children[1].children[2].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[2].getComponent(Sprite).spriteFrame=this.verde[0];
                if(this.modelos[modelo].children[1].children[3].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[3].getComponent(Sprite).spriteFrame=this.rojo[0];
                if(this.modelos[modelo].children[1].children[4].getComponent(Button).normalSprite.name!="bloqueado")
                    this.modelos[modelo].children[1].children[4].getComponent(Sprite).spriteFrame=this.naranja[1];
                break;
        }
    }
}


