import { _decorator, Component, Node, Vec3, Vec2, math, CCBoolean, Collider2D, Contact2DType, find, CCFloat, Sprite } from 'cc';
//import {GeneracionMapa} from "./GeneracionMapa"
import { SpawnMapa } from './SpawnMapa';
import { Manager } from '../Manager';
import { Objetos } from '../Objetos';
const { ccclass, property } = _decorator;

@ccclass('PlayerController')
export class PlayerController extends Component {
    @property(Node)
    roundabout: Node | null = null; // Referencia a la rotonda en el escenario

    @property(CCFloat)
    public speed: number = 200; // Velocidad lineal del coche
    @property(CCBoolean)
    private isTurning: boolean = false; // Estado de si el coche está girando
    private targetRotation: number = 0; // Rotación objetivo en grados
    private angleSpeed: number = 100; // Velocidad angular para el giro en la rotonda
    private angle: number = 0; // Ángulo actual de rotación alrededor de la rotonda
    private radius: number = 0; // Distancia entre el coche y el centro de la rotonda
    @property(Vec2)
    private center : Vec2 = null;
    private exitDirection : Vec2 = new Vec2(0,0);
    private exitRotationOffset: number = -90;
    private Inicio : boolean = false;
    @property(CCBoolean)
    private GameOver : boolean = false;

    @property(Node)
    public SpawnMapa : Node = null;

    @property(Node)
    public manager : Node = null;
    private reset : number = 10;
    



    
    protected onLoad(): void {
        this.manager = find("Canvas/Manager");
        this.SpawnMapa = find("Canvas/Generador");
        const collision = this.node.getComponent(Collider2D);
       
        if(collision){
            collision.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collision.on(Contact2DType.END_CONTACT, this.onEndCollision, this)
        }
    }
     // Método para manejar el clic y detener el giro
    Lanzame() {
        if (this.isTurning) {
            // Detener el giro y permitir el movimiento en línea recta
            this.isTurning = false;
            

            // También aplicar el giro a la rotación visual del coche
            this.node.angle += this.exitRotationOffset;
           
        }
    }
  
    update(deltaTime: number) {
        
        if (!this.isTurning && !this.Inicio) {
            // Movimiento recto en el eje Y en 2D
            this.node.setPosition(
                this.node.position.x, 
                this.node.position.y +this.speed * deltaTime
            );
        } else {
            if(!this.isTurning){
                    this.node.setPosition(this.node.position.x + this.exitDirection.x * this.speed * deltaTime,
                    this.node.position.y + this.exitDirection.y * this.speed * deltaTime
                );
               this.manager.getComponent(Manager).actualizarContador(deltaTime);
            }
            else{
                   // Movimiento de giro alrededor de la rotonda en 2D
                this.rotateAroundRoundabout(deltaTime);
            }
         
            

            
        }
    }

    // Método que detecta la colisión con la rotonda
    
        // Método que detecta la colisión con la rotonda
        onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D) {
            console.log("Choque con " +  otherCollider.group.toString() + otherCollider.node.name)
            if (otherCollider.group == 2) {
                this.manager.getComponent(Manager).tiempoRestante = this.reset;
                this.GameOver = false;
                this.speed += 10;
                if(!this.Inicio){
                    this.Inicio= true;
                }
                this.SpawnMapa.getComponent(SpawnMapa).Listo = true;
                this.roundabout = otherCollider.node;
                this.SpawnMapa.getComponent(SpawnMapa).RotondaOrigen = this.roundabout;
                this.isTurning = true;
                // Calcula el centro de la rotonda para girar alrededor
                
                this.center = new Vec2(this.roundabout.position.x,this.roundabout.position.y);          
                // Calcula la distancia entre el coche y el centro de la rotonda
                const carPosition = new Vec2(this.node.position.x, this.node.position.y);
                this.radius = Vec2.distance(this.center, carPosition);
                // Calcula el ángulo inicial del coche respecto al centro de la rotonda
                const direction = carPosition.subtract(this.center);
                this.angle = Math.atan2(direction.y, direction.x);
                
            }
            if(otherCollider.group == 1){ // choque con tarjeta suma puntos
                this.manager.getComponent(Manager).score += otherCollider.getComponent(Objetos).valor;
               
                this.manager.getComponent(Manager).ActualizaScore(otherCollider.getComponent(Objetos).valor);
                otherCollider.node.getComponent(Objetos).destruye();
            }
        }
        onEndCollision(selfCollider: Collider2D, otherCollider: Collider2D){
            
        }
        
        startTurning(center: Vec2) {
            // Obtiene la dirección desde la posición actual hacia el centro de la rotonda
            const direction = new Vec2(center.x - this.node.position.x, center.y - this.node.position.y);
            // Calcula el ángulo hacia el centro de la rotonda en radianes y lo convierte a grados
            this.targetRotation = Math.atan2(direction.y, direction.x) * (180 / Math.PI);
        }
    
        rotateAroundRoundabout(deltaTime: number) {
              // Incrementa el ángulo para simular el movimiento circular
                this.angle += this.speed * deltaTime / this.radius;

                // Calcula la nueva posición del coche en el círculo
                const x = this.center.x + this.radius * Math.cos(this.angle);
                const y = this.center.y + this.radius * Math.sin(this.angle);

                // Actualiza la posición del coche
                this.node.setPosition(x, y);
                ////----------------------------------------------------------------
                const currentRotation = this.node.angle; // Rotación actual en 2D
                const targetAngle = this.targetRotation;
                const newAngle = this.angle * (180 / Math.PI);
                this.node.angle = newAngle; // Aplica la nueva rotación
                // Si la rotación actual aún no ha alcanzado el ángulo objetivo
                /*if (Math.abs(currentRotation - targetAngle) > 90) {
                    // Interpola la rotación del coche hacia el objetivo usando lerp
                    
                } else {
                    // Cuando alcanza el ángulo objetivo, sigue rotando en círculo
                    const rotationAngle = currentRotation + this.angleSpeed * deltaTime;
                    this.node.angle = rotationAngle;
                }*/
                    this.exitDirection = new Vec2(Math.cos(this.angle), Math.sin(this.angle));
                    
                    
        }
    
       
    }



