import { _decorator, Component, Node, CCFloat, sys, CCString, Label, find, CCInteger } from 'cc';
import { Lanzame } from './Lanzame';
const { ccclass, property } = _decorator;

@ccclass('ManagerGeneral')
export class ManagerGeneral extends Component {

    @property (CCFloat) public score: number=0;
    @property(CCInteger) public contadorMonedas:number=0;
    @property(CCInteger) public contadorTarjetas:number=0;
    @property(CCString)
    public tarjetaString:string= null;
    @property(CCString)
    public monedasString:string= null;
    @property(Label) monedas:Label=null;
    @property(Label) tarjetas:Label=null;
    @property(CCFloat) public colorID:number=0;
    @property(CCFloat) public modeloID:number=0;
    @property(CCFloat) public iman:number=0;
    @property(CCFloat) public control:number=0;
    /*
    @property([CCFloat]) // Especifica que es un array de números flotantes
    miCoche: number[][] = [[0, 0]];*/

    protected onLoad(): void {
        //sys.localStorage.clear();
       
        this.LoadData();

        //this.CargaCoche();
       
        
        
    }

    start() {

        
    }

    update(dt) 
    {
        
        
    }

    
    
    Save()
    {
        console.log("Save");
        sys.localStorage.setItem('score',this.score.toString());
        sys.localStorage.setItem('monedas', this.contadorMonedas.toString());
        sys.localStorage.setItem('tarjetas', this.contadorTarjetas.toString());

        //////////////////////////POWER UPS/////////////////////////////////////
        sys.localStorage.setItem('iman', this.iman.toString());
        sys.localStorage.setItem('control', this.control.toString());
    }

    LoadData()
    {
        this.monedasString=sys.localStorage.getItem('monedas');
        this.tarjetaString=sys.localStorage.getItem('tarjetas');
        let imanP=sys.localStorage.getItem('iman');
        let controlP=sys.localStorage.getItem('control');

        if(imanP)
            {
                this.iman=parseInt(imanP);
            }
        if(controlP)
            {
                this.control=parseInt(controlP);
            }

        if(this.monedasString)
            {
                this.contadorMonedas=parseInt(this.monedasString);
                
            }

        if(this.tarjetaString)
            {
                
                this.contadorTarjetas=parseInt(this.tarjetaString);
                if(this.contadorTarjetas==null)
                {
                    this.contadorTarjetas=0;
                }
            }
    }

    ActualizaCompras(valor:number)
    {
        this.contadorTarjetas-=valor;
        /*switch(tipo)
        {
            case 'tarjeta':
               
                break;
            case 'moneda':
                this.contadorMonedas-=valor;
                break;
        }*/
    }

    CargaCoche()
    {
        /*var cocheString = sys.localStorage.getItem('coche');
        if (cocheString)
        {
            var valores = cocheString.split(',');
            this.colorID=parseInt(valores[0]);
            this.modeloID=parseInt(valores[1]);
            //this.miCoche=[[this.modeloID,this.colorID]];
            console.log("cargandoCoche");

        }*/

        var colorIDString=sys.localStorage.getItem('color');
        var modeloIDString=sys.localStorage.getItem('modelo');

        if(colorIDString)
            {
                this.colorID=parseInt(colorIDString);
                
            }
        if(modeloIDString)
            {
                this.modeloID=parseInt(modeloIDString);

            }

        
        
        //console.log("cargaCoche");
        
    }

    SaveCoche()
    {
        sys.localStorage.setItem('color',this.colorID.toString());
        
        sys.localStorage.setItem('modelo', this.modeloID.toString());

        //console.log("guardaCoche");
    }

    CocheDesbloqueado(comprado: number, nombre: string)
    {
        sys.localStorage.setItem(nombre, comprado.toString());
        //console.log("guarda meCompraron "+ nombre);
    }

    CargaCocheDesbloqueado(nombre:string, compra: number)
    {
        var meCompraron=sys.localStorage.getItem(nombre);

        if(meCompraron)
            {
                compra=parseInt(meCompraron);
                //console.log("meCompraron "+ nombre);
                
            }

            return compra;
    }
}


