import { _decorator, Component, Node, Input, input, EventMouse, RigidBody2D, Vec2, Vec3, Camera, v2, v3,Collider2D, Contact2DType, CCBoolean, CircleCollider2D, CCFloat, quat, Quat, find, Animation, misc, tween, Tween} from 'cc';
import { GiroOrbita } from './GiroOrbita';
import { ManagerGeneral } from './ManagerGeneral';
const { ccclass, property } = _decorator;

@ccclass('Lanzame')
export class Lanzame extends Component {

    
    @property(CCFloat) speed : number=400;
    @property (CCBoolean) meLanzaron:Boolean=false;
    //@property (CCBoolean)rota:Boolean=false;
    @property (Node) temp:Node=null;
    rotationSpeed: number=0.5;
    angle: number=0;
    
    @property (Node) padre:Node=null
    @property (Node) indicador:Node=null
   
  
    @property (Node) player:Node;

    @property(CCBoolean) public iman:boolean=false;
    @property(CCBoolean) public control:boolean=false;
    @property(Node) mangerG:Node=null;

    tiempoRestante:number=10;

    @property(CCBoolean) public gira: boolean=false;

    @property(CCBoolean) public enCamino:boolean=false;
    nodeNull:Node=null;
    @property(CCBoolean) public instanciar:boolean=false;

    rotationTween: Tween<Node>;
    //@property moveSpeed:Number=1000;
    onLoad()
    {
        this.node.on(Input.EventType.TOUCH_END,this.Lanza,this)
        input.on(Input.EventType.MOUSE_UP, this.LanzaMouse, this);

        this.mangerG=find("Canvas/ManagerGeneral");

        if(this.mangerG.getComponent(ManagerGeneral).iman==0)
         {
            this.iman=false;

         }
        else if(this.mangerG.getComponent(ManagerGeneral).iman==1)
         {
            this.iman=true;
         }

         if(this.mangerG.getComponent(ManagerGeneral).control==0)
        {
            this.control=false;
   
        }
        else if(this.mangerG.getComponent(ManagerGeneral).control==1)
        {
            this.control=true;
        }

        
        this.player=this.node;
        ////COLLIDER////
        let collider = this.getComponent(Collider2D);
        if (collider) 
        {
            // Called only once when two colliding bodies start to make contact
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
 
        }
    }
    start() {

        this.indicador.position=this.node.position;
        
    }

    update(dt) {
        
        if(this.meLanzaron)
        {
            
            this.Mover(dt);

           
        }

        if(this.control)
            {
                this.ControlPowerUp(dt);
            }
            if(this.gira&& !this.rotationTween)
            {
                console.log("entre");
                this.player.parent=this.padre;
                this.node.setWorldPosition(this.indicador.getWorldPosition());
                this.startRotation();
                /*this.rotationTween.call(() => {
                    // Acciones a realizar cuando el tween termina
                    console.log("El tween ha terminado.");

                    this.stopRotation();
                })*/
            }
        
    }

    startRotation () 
    {
        // Crea una nueva instancia de la animación tween
        this.rotationTween = tween(this.node)
            // Establece la duración de la animación y define la rotación
            .to(0.5, { angle: this.indicador.angle })

            this.rotationTween.call(() => {
                // Acciones a realizar cuando el tween termina
                //console.log("El tween ha terminado.");

                this.enCamino=true;
                this.meLanzaron=true;
                this.gira=false;
            })
            // Repite la animación indefinidamente
            //.repeatForever()
            // Comienza la animación
            .start();

           
    }
   
    stopRotation() {
        if (this.rotationTween) {
            this.rotationTween.stop();
            this.rotationTween = null; // Limpia la variable de tween
        }
    }

   ControlPowerUp(dt)
   {
        if (this.tiempoRestante > 0) 
        {
            this.tiempoRestante-=dt; // Reduce el tiempo restante
            
        }
        else 
        {
            this.temp.getComponent(GiroOrbita).rotationSpeed=0.5;
            this.control=false;
            this.mangerG.getComponent(ManagerGeneral).control=0;
            this.mangerG.getComponent(ManagerGeneral).Save();
        }
        if(this.temp!=null&&this.tiempoRestante>0)
        {
            this.temp.getComponent(GiroOrbita).rotationSpeed=0.1;
        }
   }
    

    Lanza()
    {
        if(this.temp!=null)
            {
               
                this.player.parent=this.padre;
                
                this.node.setWorldPosition(this.indicador.getWorldPosition());
               
                this.gira=true;
                
               
            }
            else
            {
                this.meLanzaron=true;
            }
    }


   

    Mover(dt)
    {
       
        var localUpDirection = this.node.up;

        // Calcula el desplazamiento multiplicando la dirección local por la velocidad
        var displacement = localUpDirection.multiplyScalar(this.speed*dt);
       
        // Mueve el nodo a lo largo de su eje local hacia arriba
        this.node.position = this.node.position.add(displacement);
    }
    LanzaMouse(event: EventMouse)
    {
        if (event.getButton() === 0) 
        {
            
            if(this.temp!=null)
            {
                //this.temp.getComponent(GiroOrbita).EstoySolo();
                
               //this.nodeNull.setWorldRotation(this.indicador.getWorldRotation());
                 
                 this.gira=true;
                 //this.instanciar=false;
                 this.stopRotation();
            }
            else
            {
               
                this.meLanzaron=true;
            }
                
           
      
        }
    }

    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D)
    {
        //console.log(otherCollider.name+otherCollider.group);
        if(otherCollider.group===2)
        {
            if(this.node.parent!=otherCollider.node||this.node.parent==this.padre)
            {  
                this.enCamino=false;
                this.angle=otherCollider.node.angle;
                this.instanciar=true;

               
                    this.temp=otherCollider.node;
                    this.node.parent=this.temp;

                
                //this.rota=true;
                    this.meLanzaron=false;//evita que se mueva

                    this.enCamino=false;  
               if(otherCollider.getComponent(GiroOrbita).primeraVez)
               {//console.log("mePegue");
                    
                    otherCollider.getComponent(GiroOrbita).primeraVez=false;
               }
                
                //
                

            }
            

            
        }
        if(otherCollider.group===16)
            {
                
            }
        
        
    }

    onEndContact(selfCollider: Collider2D, otherCollider: Collider2D)
    {
        //console.log("termino el contacto");

        //this.enCamino=false;
        
        
        
    }

}


