import { _decorator, Component, Node, find, Label } from 'cc';
import { ManagerGeneral } from '../ManagerGeneral';
const { ccclass, property } = _decorator;

@ccclass('Cantidad')
export class Cantidad extends Component {
    @property(Node)
    public manager : Node;
    private Texto : Node;

    protected onLoad(): void {
        this.manager= find("Canvas/ManagerGeneral")
        this.Texto= find("Canvas/cantidad")
    }
    start() {
        this.Texto.getComponent(Label).string = this.manager.getComponent(ManagerGeneral).contadorTarjetas.toString();
    }

    update(deltaTime: number) {
        
    }
}


