import { _decorator, Component, Node, find, Button, Sprite, Color, color, Label, CCFloat, CCString, SpriteFrame, CCBoolean, CCInteger } from 'cc';
import { ManagerGeneral } from './ManagerGeneral';
import { SeleccionColor } from './SeleccionColor';
const { ccclass, property } = _decorator;

@ccclass('CompraColor')
export class CompraColor extends Component 
{
    @property(Node) subMenu:Node=null;
    @property(Label) costoString:Label=null;
    @property(Node) subSprite:Node=null;

    /////////////////////////////////////////////
    @property(CCInteger) costo:number=0;
    @property (SpriteFrame) colorFinal:SpriteFrame=null;
    @property (SpriteFrame) colorModelo:SpriteFrame=null;
    @property(Node) coche:Node;

    ////////////////////////////////////////////////
    @property(CCFloat) miColorID:number;
    @property(CCFloat) miModeloID:number;
    
    /////////////////////////////////////////////////
    @property (Button) botonCompra: Button;
    @property(Button) cerrar:Button;

    managerG:ManagerGeneral;
    ma:Node=null;

    @property(Node) botones:Node=null;
    @property(CCString) colors:string="";
    @property(Color) colorTexto:Color=null;
    @property(Color) colorTextoNormal=null;
    
    @property(CCString) nombre:string="";
    @property (CCFloat) desbloqueado:number=0;
    @property(SpriteFrame) normalFrame: SpriteFrame=null;

    @property(CCBoolean) tarjeta:boolean=false;
    

    protected onLoad(): void 
    {
        this.ma=find("Canvas/ManagerGeneral");
        //this.botones=find("Canvas/Botones");
        this.managerG=this.ma.getComponent(ManagerGeneral);
        this.subMenu.active=false;

        this.desbloqueado=this.managerG.CargaCocheDesbloqueado(this.nombre,this.desbloqueado);

        
        
        this.botonCompra.node.on(Button.EventType.CLICK, this.Compra, this);
        this.cerrar.node.on(Button.EventType.CLICK, this.Cerrar, this);

    }

    start() {

        if(this.desbloqueado==1)
            {
                console.log("ya me desbloquearon");
                this.node.getComponent(Sprite).spriteFrame=this.normalFrame;
                this.node.getComponent(Button).normalSprite=this.normalFrame;
            }
    }

    update(deltaTime: number) {
        if(this.node.getComponent(Sprite).spriteFrame==this.colorFinal)
            {
                this.managerG.colorID=this.miColorID;
                this.managerG.modeloID=this.miModeloID;

                this.managerG.SaveCoche();
                this.managerG.LoadData();
            }
            if(this.botonCompra.node.active)
                {
                    if(this.botonCompra.getComponent(Sprite).spriteFrame==this.botonCompra.getComponent(Button).hoverSprite)
                        {
                            this.costoString.color=this.colorTexto;
                        }
                        else
                        {
                            this.costoString.color=this.colorTextoNormal;
                        }
                }
       
        
    }

    Cerrar()
    {
        this.subMenu.active=false;
        this.botones.active=true;
        this.botonCompra.node.active=false;
    }

    Seleccion()
    {
        if(this.node.getComponent(Sprite).spriteFrame.name=='bloqueado')
        {
            this.subMenu.active=true;
            this.botonCompra.node.active=true;
            this.costoString.string=this.costo.toString();
            this.botones.active=false;
            this.subSprite.getComponent(Sprite).spriteFrame=this.colorModelo;
            this.MeAlcanza();
        }
        else
        {
            this.coche.getComponent(Sprite).spriteFrame=this.colorModelo;
            this.node.getComponent(Button).pressedSprite=this.colorFinal;
            this.node.getComponent(Button).normalSprite=this.colorFinal;
            this.ma.getComponent(SeleccionColor).Colores(this.colors);
            //Selecciona este color
            this.managerG.colorID=this.miColorID;
            this.managerG.modeloID=this.miModeloID;
            this.managerG.SaveCoche();
        }
    }

    MeAlcanza()
    {
        if(!this.tarjeta)
        {
            if(this.costo<=this.managerG.contadorMonedas)
                {
                    this.botonCompra.interactable=true;
                }
                else
                {
                    this.botonCompra.interactable=false;
                    this.botonCompra.onDisable();
                }     
        }
        else if(this.tarjeta)
            {
                if(this.costo<=this.managerG.contadorTarjetas)
                    {
                        this.botonCompra.interactable=true;
                    }
                    else
                    {
                        this.botonCompra.interactable=false;
                        this.botonCompra.onDisable();
                    }     
            }
        
    }

    Compra()
    {
        console.log("valor de compra " + this.costo.toString());
        this.managerG.ActualizaCompras(this.costo);   
        this.costoString.color=this.colorTexto;
        this.managerG.Save();
        this.Comprado();
        //this.candadoIcon.active=false;

        //this.managerG.modeloID=this.miModeloID;
        //this.managerG.colorID=this.miColorID;
        this.managerG.SaveCoche();
        this.managerG.LoadData();
        
    }

    Comprado()
    {
        
        this.node.getComponent(Sprite).spriteFrame=this.colorFinal;
        console.log(this.colorModelo.name);
        this.coche.getComponent(Sprite).spriteFrame=this.colorModelo;
        this.node.getComponent(Button).normalSprite=this.colorFinal;
        this.node.getComponent(Button).pressedSprite=this.colorFinal;
        this.subMenu.active=false;
        this.botones.active=true;
        this.botonCompra.node.active=false;
        this.costoString.color=this.colorTextoNormal;
        this.managerG.colorID=this.miColorID;
        this.managerG.modeloID=this.miModeloID;
        this.ma.getComponent(SeleccionColor).Colores(this.colors);

        this.managerG.CocheDesbloqueado(1,this.nombre);

        
        
    }
}


