import { _decorator, CCBoolean, Component, find, instantiate, Node, Prefab, randomRangeInt, Vec2, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('SpawnMapa')
export class SpawnMapa extends Component {

    @property(Prefab)
    public ListaPrefab : Prefab [] = [];
    @property(Node)
    public RotondaOrigen : Node = null;
    @property(CCBoolean)
    public Listo : boolean = false;
    private camino : number = 0;
    @property(CCBoolean)
    private inicio : boolean = false;
    protected onLoad(): void {
        this.inicio = true;
        this.RotondaOrigen = find ("Canvas/Generador/Orbita");
    }
    start() {

    }
    instanciaCaminos(){
        if ( this.inicio){
            this.inicio = false;
            this.camino = randomRangeInt(0,2);
            switch(this.camino){
                case 0: 
                
                    const node = instantiate(this.ListaPrefab[0]);
                    node.parent = this.node;
                    node.position = new Vec3( this.RotondaOrigen.position.x-270, this.RotondaOrigen.position.y +515, 0);
                    node.angle = 5;
                    this.camino = 1;
                    
                
                
                
                break;
                case 1:
                    
                        const node1 = instantiate(this.ListaPrefab[1]);
                        node1.parent = this.node;
                        node1.position = new Vec3( this.RotondaOrigen.position.x +270, this.RotondaOrigen.position.y +515, 0);
                        node1.angle = -5;
                        this.camino=0;
                    
                
               
                break;
            }
        }
        else{
            switch(this.camino){
                case 0: 
                
                    const node = instantiate(this.ListaPrefab[0]);
                    node.parent = this.node;
                    node.position = new Vec3( this.RotondaOrigen.position.x-270, this.RotondaOrigen.position.y +515, 0);
                    node.angle = 5;
                    this.camino = 1;
                    
                
                
                
                break;
                case 1:
                    
                        const node1 = instantiate(this.ListaPrefab[1]);
                        node1.parent = this.node;
                        node1.position = new Vec3( this.RotondaOrigen.position.x +270, this.RotondaOrigen.position.y +515, 0);
                        node1.angle = -5;
                        this.camino=0;
                    
                
               
                break;
            }
        }
       
    }

    update(deltaTime: number) {

        if(this.Listo){
            this.Listo = false;
            this.instanciaCaminos();
            
        }
        
    }
}


