import { _decorator, CCBoolean, CCFloat, Component, find, Label, Node, SpriteFrame, sys, Sprite, CCInteger, Button } from 'cc';
const { ccclass, property } = _decorator;
import { ManagerGeneral } from './ManagerGeneral';
import { ButtonMessage } from './ButtonMessage';

@ccclass('Manager')
export class Manager extends Component {

    @property(Label) scoreText: Label=null;
    @property(Label) final_scoreText: Label=null;
    @property (CCInteger) public score: number=0;
    @property(CCFloat) public contadorMonedas:number=0;
    @property(CCInteger) public contadorTarjetas:number=0;
    gameOver:Node=null;
    @property(Node) player:Node=null;
    @property(CCFloat) public creditoTotal:number=0;

    @property(ManagerGeneral)managerG:ManagerGeneral=null;
    @property(Node)
    public post : Node = null;
    //@property(CCBoolean) public gameOver:boolean=false;
    a:Node=null;
    @property(CCBoolean) public moneda:boolean;

    @property(CCFloat) modeloCoche:number=0;
    @property([SpriteFrame]) coloresCoche01:SpriteFrame[]=[];
    @property([SpriteFrame]) coloresCoche02:SpriteFrame[]=[];
    @property([SpriteFrame]) coloresCoche03:SpriteFrame[]=[];
    //@property(Node) player:Node=null;
    @property(CCFloat)
    public tiempoRestante = 10;

    @property(CCFloat) miColor:number=0;

    protected onLoad(): void 
    {
        this.player=find("Canvas/Player");
        this.gameOver=find("Canvas/Fondo/GameOver");
        this.a=find("Canvas/ManagerGeneral");
        this.managerG=this.a.getComponent(ManagerGeneral);
        //this.managerG.LoadData();
        
        this.managerG.CargaCoche();

        this.modeloCoche=this.managerG.modeloID;
        this.miColor=this.managerG.colorID;
        this.MiCoche();
        
        this.gameOver.active=false;
    }
    start() {
        this.contadorMonedas=this.managerG.contadorMonedas;
        this.contadorTarjetas=0;

    }

    MiCoche()
    {
        switch(this.modeloCoche)
        {
            case 0:
                this.player.getComponent(Sprite).spriteFrame=this.coloresCoche01[this.miColor];
                //console.log("cambio de color");
                break;
            case 1:
                this.player.getComponent(Sprite).spriteFrame=this.coloresCoche02[this.miColor];
                //console.log("cambio de color");
                break;
            case 2:
                this.player.getComponent(Sprite).spriteFrame=this.coloresCoche03[this.miColor];
                //console.log("cambio de color");
                break;
        }
        
    }


    update(deltaTime: number) {
        
        
    }

    ActualizaScore( valor : number)
    {
        this.scoreText.string=this.score.toString();
        this.managerG.score=this.score;

        /*
        if(moneda)
            {
                this.managerG.contadorMonedas=this.contadorMonedas;
                console.log(this.managerG.contadorMonedas);


            }
            else
            {
                //this.managerG.contadorTarjetas+=1;
                this.managerG.contadorTarjetas=this.contadorTarjetas;
                console.log(this.managerG.contadorTarjetas);
            }*/
        this.managerG.contadorTarjetas+=valor;
        console.log(this.managerG.contadorTarjetas);
        
        this.managerG.Save();

    }

    actualizarContador(dt) 
    {
        if (this.tiempoRestante > 0) {
            this.tiempoRestante-=dt; // Reduce el tiempo restante
            
        } else {
            
            this.GameOver();
            // Aquí puedes hacer lo que necesites cuando el contador llega a cero
            this.unschedule(this.actualizarContador); // Detiene el temporizador
        }
    }

    GameOver()
    {
        this.gameOver.active=true;
        this.post.getComponent(ButtonMessage).sendMessageToParent();
        this.player.active=false;
        this.final_scoreText.string=this.score.toString();
        this.managerG.score=this.score;
        this.managerG.Save();
    }
}


