import { _decorator, Component, Node, Vec3 } from 'cc';
import { Manager } from './Manager';
import { Lanzame } from './Lanzame';
const { ccclass, property } = _decorator;

@ccclass('FollowPlayer')
export class FollowPlayer extends Component {
    @property(Node)
    public player : Node;
    enPosicion:Boolean=false;
    inicio: Boolean=true;
    temp:Node=null;
    tiempoRestante:number=10;
    reset:number=0;
    @property(Manager)manager:Manager=null;

    protected onLoad(): void {
        this.reset=this.tiempoRestante;
    }
    start() {

    }

    update(dt) {
        //
        if(this.player.parent.name=="Canvas"&&!this.inicio)
        {
            if(this.player.getComponent(Lanzame).enCamino)
            {
                //console.log("SIGO COCHE");
                this.node.position = this.player.position;

                this.actualizarContador(dt);
           
                //this.player.getComponent(Lanzame).enCamino=false;
            }
        }
        else if(this.player.parent.name!="Canvas"&&this.temp!=this.player.parent)
        {
            this.tiempoRestante=this.reset;
            this.temp=this.player.parent;
            this.inicio=false;
            let newPosition = this.node.position.lerp(this.player.parent.getWorldPosition(), 2 * dt);
            //this.node.position.lerp(this.node.getWorldPosition(),0,this.player.parent.getWorldPosition())
            //this.node.setWorldPosition(newPosition);
            this.node.position = newPosition;
            console.log(this.player.parent.name);
            
            
        }
    }
    actualizarContador(dt) 
    {
        if (this.tiempoRestante > 0) {
            this.tiempoRestante-=dt; // Reduce el tiempo restante
            
        } else {
            
            this.manager.GameOver();
            // Aquí puedes hacer lo que necesites cuando el contador llega a cero
            this.unschedule(this.actualizarContador); // Detiene el temporizador
        }
    }
}


